import { useState } from 'react';
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import ArrowCircleRightIcon from '@mui/icons-material/ArrowCircleRight';
import gogol from 'static/images/gogol.jpg';
import sagnika from 'static/images/sagnika.jpg';
import nabarun from 'static/images/blog_pic.jpg';

export default function About() {
  const [text, setText] = useState('Read Our Story');
  const [display, setDisplay] = useState('none');

  const handleStoryClick = () => {
    if (display === 'none') {
      setText('Collapse Story');
      setDisplay('block');
    }
    if (display === 'block') {
      setText('Read Our Story');
      setDisplay('none');
    }
  };

  return (
    <div className="px-4 py-16 mx-auto w-full bg-white md:px-24 lg:px-8 lg:py-20">
      <div className="max-w-xl mb-10 md:mx-auto xs:text-center lg:max-w-2xl md:mb-12">
        <h2 className="max-w-lg mb-6 font-sans text-3xl font-bold leading-none tracking-tight text-gray-900 xs:text-4xl md:mx-auto" style={{ textAlign: 'center' }}>
          The family that built myMemoryStore
        </h2>
        <Stack direction="column" alignItems="center" justifyContent="center">
          <div style={{ display }}>
            <Typography>
              When our son was born 2 years back, we started clicking pictures like crazy.
              Sadly enough could not find them when related conversations arose.
              Most photo storage solutions out there felt like empty boxes where you dump all
              photos.
              We kept the most precious photos along with the misclicks, the hazy ones and the
              ones
              we could not remember why we took. We kept the photos but what we really wanted to
              keep
              were the memories. But what are photos without the associated emotions and the
              stories.
              That is why we started building myMemoryStore, where you can keep your precious
              photos
              and the stories related to them so that you can relive the memories whenever you
              want.
            </Typography>
          </div>
          <Button sx={{ textAlign: 'center' }} onClick={handleStoryClick}>
            {text}
            {' '}
            <ArrowCircleRightIcon />
          </Button>
        </Stack>
      </div>
      <div className="grid gap-10 mx-auto xs:grid-cols-1 md:grid-cols-3 lg:max-w-screen-lg">
        <div>
          <div className="relative pb-56 mb-4 rounded shadow lg:pb-64">
            <img
              src={nabarun}
              alt="Person"
              className="absolute object-cover w-full h-full rounded"
            />
          </div>
          <div className="flex flex-col xs:text-center">
            <p className="text-lg font-bold text-gray-900 text-center">Nabarun Chatterjee</p>
            <p className="mb-5 text-xs text-gray-900 text-center">Product</p>
          </div>
        </div>
        <div>
          <div className="relative pb-56 mb-4 rounded shadow lg:pb-64">
            <img
              src={gogol}
              alt="Person"
              className="absolute object-cover w-full h-full rounded"
            />
          </div>
          <div className="flex flex-col xs:text-center">
            <p className="text-lg font-bold text-gray-900 text-center">Shlok Chatterjee</p>
            <p className="mb-5 text-xs text-gray-900 text-center">Chief Chaos Creator</p>
          </div>
        </div>
        <div>
          <div className="relative pb-56 mb-4 rounded shadow lg:pb-64">
            <img
              src={sagnika}
              alt="Person"
              className="absolute object-cover w-full h-full rounded"
            />
          </div>
          <div className="flex flex-col sm:text-center">
            <p className="text-lg font-bold text-gray-900 text-center">Sagnika Dasgupta</p>
            <p className="mb-5 text-xs text-gray-900 text-center">Creative Lead</p>
          </div>
        </div>
      </div>
    </div>
  );
}
