import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import PropTypes from 'prop-types';

export default function LoginButton({ authURL, next }) {
  const url = (next !== '') ? `https://${authURL}/oauth2/authorize?next=${next}` : `https://${authURL}/oauth2/authorize`;
  return (
    <a
      href={url}
      target="_self"
      style={{ textDecoration: 'none', color: 'white' }}
    >
      <Button
        type="submit"
        variant="contained"
        sx={{
          borderRadius: '5px',
          backgroundColor: 'white',
          '&:hover': {
            backgroundColor: 'form.input',
          },
        }}
      >
        <Typography variant="h6" color="primary">Login</Typography>
      </Button>
    </a>
  );
}

LoginButton.propTypes = {
  authURL: PropTypes.string.isRequired,
  next: PropTypes.string.isRequired,
};
